export default {
	data() {
		return {
			demolist:[
				'https://fakeimg.pl/250x400/?text=World',
				'https://fakeimg.pl/350x400/?text=World',
				'https://fakeimg.pl/350x500/?text=World',
				'https://fakeimg.pl/250x400/?text=World',
				'https://fakeimg.pl/350x500/?text=World',
				'https://fakeimg.pl/250x400/?text=World',
			],
			list:[
				'https://fakeimg.pl/250x400/?text=World',
				'https://fakeimg.pl/350x400/?text=World',
				'https://fakeimg.pl/350x500/?text=World',
				'https://fakeimg.pl/250x400/?text=World',
				'https://fakeimg.pl/250x400/?text=World',
				'https://fakeimg.pl/350x400/?text=World',
			],
		}
	},
	methods: {
		Init(){
			this.list = this.demolist;
		},
		Sell(){
			this.list = this.demolist;
		},
		Price(){
			this.list = this.demolist;
		}
	},
	onReachBottom(){
		uni.showLoading({
		    title: '加载中'
		});
		if(this.list.length > 30){
			uni.showToast({
				icon:'none',
			    title: '暂无更多',
			    duration: 2000
			});
			return;
		}
		setTimeout(()=>{
			this.list = this.list.concat(this.demolist);
		    uni.hideLoading();
		}, 300);
	}
}
